* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #ccc;
}

.content {
    flex: 1;
    padding: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.display-content-1 {
    width: 40%;
    min-width: 400px;
    margin: 2% auto;
    padding: 2%;
    background-color: white;
    border-radius: 10px;
    text-align: center;
}

.content h1 {
    padding: 2%;
    border-bottom: 1px solid black;
}

.content h2 {
    padding: 4%;
}

.content input[type=text] {
    margin: 10px auto;
    display: block;
    width: 50%;
    min-width: 200px;
    padding: 15px;
    border: 1px solid black;
    border-radius: 10px;
}

@media screen and (max-width:450px) {
    .content {
        padding: 10%;
    }

    .display-content-1 {
        min-width: 300px;
    }
}

@media screen and (max-width:450px) {

    .display-content-1 {
        min-width: 250px;
    }
}