* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.display-wrraper {
    width: 100%;
    padding: 2%;
    background-color: #ccc;
}

.display-content {
    width: 80%;
    padding: 2%;
    margin: 0 auto;
    background-color: white;
    border-radius: 10px;
}

.display-resultado {
    width: 15%;
    min-width: 200px;
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    z-index: 999;
    border-radius: 10px;
}

.display-resultado h2 {
    padding-bottom: 10px;
    border-bottom: 1px solid black;
}

.display-resultado p {
    padding-top: 10px;
}

.display-resultado.active {
    display: block;
}

.overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 998;
}

.overlay.active {
    display: block;
}

.close {
    width: 20px;
    position: absolute;
    top: 0px;
    right: 0px;
    cursor: pointer;
    border-top-right-radius: 10px;
}

.close:hover {
    color: white;
    background-color: red;
}

.close h2 {
    margin-top: 0;
}

.close p {
    margin-bottom: 0;
}

.display-header {
    font-size: 30px;
    text-align: center;
    padding-bottom: 2%;
    border-bottom: 1px solid black;
}

.display-title {
    text-align: center;
    padding: 2%;
    font-size: 20px;
    background-color: #ccc;
    border: 1px solid black;
    margin-bottom: 2%;
}

.display-placar {
    text-align: center;
    width: 20%;
    margin: 0 auto;
    background-color: rgb(93, 93, 255);
    border-radius: 10px;
    color: white;
}

.pontos {
    padding: 10px;
    border-bottom: 1px solid black;
    font-size: 20px;
}

.pontos-numero {
    display: flex;
    padding: 10px;
    font-size: 20px;
}

.pontos-jogador-1 {
    width: 40%;
}

.divisao-dos-pontos {
    width: 20%;
}

.pontos-jogador-2 {
    width: 40%;
}

.display-jogadores {
    display: flex;
    padding: 2%;
}

.display-jogadores img {
    width: 30%;
    padding: 2%;
}

.display-jogador-1 {
    text-align: left;
    cursor: pointer;
}

.display-jogador-1 h2 {
    text-align: center;
    width: 90%;
    background-color: #ccc;
    border-radius: 10px;
}

.display-jogador-2 {
    text-align: right;
}

.display-jogador-2 h2 {
    width: 90%;
    margin-left: 10%;
    text-align: center;
    background-color: #ccc;
    border-radius: 10px;
}

@media screen and (max-width:700px) {
    .display-placar {
        width: 50%;
    }

    .display-images img {
        display: block;
    }

    .display-jogadores img {
        width: 50%;
    }

    .display-header {
        font-size: 20px;
    }
}

@media screen and (max-width:400px) {
    .display-jogador-1 h2 {
        font-size: 15px;
    }

    .display-jogador-2 h2 {
        font-size: 15px;
    }

    .pontos {
        font-size: 15px;
    }

    .pontos-numero {
        font-size: 15px;
    }

    .display-header {
        font-size: 15px;
    }

    .display-title {
        font-size: 15px;
    }
}