* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.display-wrraper-X1 {
    width: 100%;
    padding: 2%;
    background-color: #ccc;
}

.display-content-X1 {
    flex: 1;
    width: 80%;
    padding: 2%;
    margin: 0 auto;
    background-color: white;
    border-radius: 10px;
}

.display-resultado {
    width: 15%;
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    z-index: 999;
    border-radius: 10px;
}

.display-resultado h2 {
    padding-bottom: 10px;
    border-bottom: 1px solid black;
}

.display-resultado p {
    padding-top: 10px;
}

.display-resultado.active {
    display: block;
}

.display-decision {
    width: 50%;
    min-width: 270px;
    height: 50%;
    min-height: 550px;
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    z-index: 999;
    border-radius: 10px;
}

.display-decision.active {
    display: block;
}

.display-decision h2 {
    padding: 20px;
    border-bottom: 1px solid black;
}

.display-decision p {
    padding: 30px;
    font-size: 30px;
}

.display-decision button {
    width: 15%;
    min-width: 100px;
    padding: 10px;
    font-size: 20px;
    background-color: rgb(93, 93, 255);
    border: 1px solid #ccc;
    border-radius: 10px;
    cursor: pointer;
    color: white;
}

.display-decision button:hover {
    background-color: rgb(31, 31, 255);
}

.overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 998;
}

.overlay.active {
    display: block;
}

.overlay-decision {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 998;
}

.overlay-decision.active {
    display: block;
}

.close {
    width: 20px;
    position: absolute;
    top: 0px;
    right: 0px;
    cursor: pointer;
    border-top-right-radius: 10px;
}

.close:hover {
    color: white;
    background-color: red;
}

.close h2 {
    margin-top: 0;
}

.close p {
    margin-bottom: 0;
}

.display-header-X1 {
    font-size: 30px;
    text-align: center;
    padding-bottom: 2%;
    border-bottom: 1px solid black;
}

.display-title {
    text-align: center;
    padding: 2%;
    font-size: 20px;
    background-color: #ccc;
    border: 1px solid black;
    margin-bottom: 2%;
}

.display-placar-X1 {
    text-align: center;
    width: 20%;
    margin: 0 auto;
    background-color: rgb(93, 93, 255);
    border-radius: 10px;
    color: white;
}

.pontos-X1 {
    padding: 10px;
    border-bottom: 1px solid black;
    font-size: 20px;
}

.pontos-numero-X1 {
    display: flex;
    padding: 10px;
    font-size: 20px;
}

.pontos-jogador-1 {
    width: 40%;
}

.divisao-dos-pontos {
    width: 20%;
}

.pontos-jogador-2 {
    width: 40%;
}

.display-jogadores-X1 {
    display: flex;
    padding: 2%;
}

.display-jogadores-X1 img {
    width: 30%;
    padding: 2%;
    margin: 0 auto;
}

.display-jogador-1 {
    cursor: pointer;
}

.display-jogador-1.active-escolhido {
    padding: 10px;
    background-color: rgba(109, 109, 109, 0.3);
    z-index: 998;
    border-radius: 10px;
}

.display-jogador-1 h2 {
    margin: 0 auto;
    text-align: center;
    width: 90%;
    background-color: #ccc;
    border-radius: 10px;
}

.mensagem-escolhido-X1 {
    display: none;
    position: absolute;
    width: 15%;
    min-width: 150px;
    margin-left: 10%;
    margin-top: 7%;
    background-color: #fff;
    padding: 20px 0;
    border: 1px solid #ccc;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    z-index: 999;
    border-radius: 10px;
}

.mensagem-escolhido-X1.active {
    display: block;
}

.mensagem-escolhido-1-X1 {
    display: none;
    position: absolute;
    width: 15%;
    min-width: 150px;
    margin-left: 10%;
    margin-top: 7%;
    background-color: #fff;
    padding: 20px 0;
    border: 1px solid #ccc;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    z-index: 999;
    border-radius: 10px;
}

.mensagem-escolhido-1-X1.active {
    display: block;
}

.display-images {
    margin: 0 auto;
}

.display-jogador-2-X1 {
    cursor: pointer;
}

.display-jogador-2-X1 h2 {
    margin: 0 auto;
    text-align: center;
    width: 90%;
    background-color: #ccc;
    border-radius: 10px;
}

.display-jogador-2-X1 p {
    text-align: right;
}

.display-jogador-2-X1.active-escolhido {
    padding: 10px;
    background-color: rgba(109, 109, 109, 0.3);
    z-index: 998;
    border-radius: 10px;
}

.opacity-display {
    opacity: 0.3;
}

@media screen and (max-width:800px) {
    .display-decision {
        min-height: 650px;
    }
}

@media screen and (max-width:700px) {
    .display-placar-X1 {
        width: 50%;
    }

    .display-images img {
        display: block;
    }

    .display-jogadores-X1 img {
        width: 50%;
    }

    .display-header-X1 {
        font-size: 20px;
    }

    .display-decision p {
        font-size: 20px;
    }

    .display-decision {
        min-height: 550px;
    }

    .mensagem-escolhido-X1 {
        margin-left: 5%;
        margin-top: 25%;
    }

    .mensagem-escolhido-1-X1 {
        margin-left: 5%;
        margin-top: 25%;
    }
}

@media screen and (max-width:600px) {
    .display-decision p {
        font-size: 18px;
    }

    .mensagem-escolhido-X1 {
        margin-left: 4%;
        margin-top: 25%;
        font-size: 15px;
    }

    .mensagem-escolhido-1-X1 {
        margin-left: 4%;
        margin-top: 25%;
        font-size: 15px;
    }
}

@media screen and (max-width:500px) {
    .mensagem-escolhido-X1 {
        margin-left: 4%;
        margin-top: 25%;
        min-width: 120px;
    }

    .mensagem-escolhido-1-X1 {
        margin-left: 4%;
        margin-top: 25%;
        min-width: 130px;
    }
}

@media screen and (max-width:450px) {
    .mensagem-escolhido-X1 {
        margin-left: 3%;
    }

    .mensagem-escolhido-1-X1 {
        margin-left: 3%;
    }
}

@media screen and (max-width:400px) {
    .mensagem-escolhido-X1 {
        margin-left: 4%;
        min-width: 100px;
        font-size: 12px;
    }

    .mensagem-escolhido-1-X1 {
        margin-left: 4%;
        min-width: 100px;
        font-size: 12px;
    }

    .display-jogador-1 h2 {
        font-size: 15px;
    }

    .display-jogador-2-X1 h2 {
        font-size: 15px;
    }

    .pontos-X1 {
        font-size: 15px;
    }

    .pontos-numero-X1 {
        font-size: 15px;
    }

    .display-header-X1 {
        font-size: 15px;
    }
}

@media screen and (max-width:350px) {
    .mensagem-escolhido-X1 {
        margin-left: 2%;
        min-width: 90px;
        font-size: 12px;
    }

    .mensagem-escolhido-1-X1 {
        margin-left: 2%;
        min-width: 90px;
        font-size: 12px;
    }
}

@media screen and (max-width:300px) {
    .mensagem-escolhido-X1 {
        margin-left: 1%;
        min-width: 90px;
        font-size: 12px;
    }

    .mensagem-escolhido-1-X1 {
        min-width: 90px;
        font-size: 12px;
    }
}