.link {
    padding-bottom: 3%;
    text-align: center;
    background-color: #ccc;
}

.link-inside {
    padding: 10px 40px;
    text-decoration: none;
    color: white;
    background-color: rgb(93, 93, 255);
    border-radius: 10px;
}

.link-inside:hover {
    background-color: rgb(31, 31, 255)
}

.link-home {
    width: 100%;
    padding: 20px;
    text-align: center;
}

.wrapper-X1 {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #ccc;
}

.content-X1 {
    flex: 1;
    padding: 2% 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:400px) {
    .link {
        padding-bottom: 5%;
    }
}